body, html {
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f5f5f5;
    font-family: Arial, sans-serif;
}

.centered-div {
    background-color: #ffffff;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    text-align: center;
    min-width: 500px;
    max-width: 700px;
    margin: 10px;
    word-break: break-word;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 10px 0;
    color: #333;
}

h1 {
    font-size: 2.5em;
    font-weight: 700;
    color: #2c3e50;
    border-bottom: 2px solid #2c3e50;
    padding-bottom: 10px;
    margin-bottom: 20px;
}

h2 {
    font-size: 2em;
    font-weight: 600;
    color: #34495e;
    padding-bottom: 8px;
    margin-bottom: 18px;
}

h3 {
    font-size: 1.75em;
    font-weight: 500;
    color: #5d6767;
    padding-bottom: 6px;
    margin-bottom: 16px;
}

h4 {
    font-size: 1.5em;
    font-weight: 500;
    color: #95a5a6;
    margin-bottom: 14px;
}

h5 {
    font-size: 1.25em;
    font-weight: 400;
    color: #bdc3c7;
    margin-bottom: 12px;
}

h6 {
    font-size: 1em;
    font-weight: 400;
    color: #ecf0f1;
    margin-bottom: 10px;
}

.spaceBetween {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

span {
    font-family: Arial, sans-serif;
    font-size: 22px;
    color: darkslateblue;
}

span.highlight {
    background-color: #ffff99; /* Светло-желтый фон */
    font-weight: bold;
    color: #000;
}

/* Стили для ошибок */
span.error {
    color: #ff0000; /* Красный текст */
    font-weight: bold;
    font-size: 14px;
}

/* Стили для информации */
span.info {
    color: dodgerblue; /* Синий текст */
    font-size: 22px;
}

/* Стили для предупреждений */
span.warning {
    color: #ffa500; /* Оранжевый текст */
    font-weight: bold;
    font-size: 14px;
}

/* Стили для успеха */
span.success {
    color: #28a745; /* Зеленый текст */
    font-weight: bold;
    font-size: 14px;
}

/* Пример использования градиента для текста */
span.gradient {
    background: linear-gradient(to right, #ff7e5f, #feb47b);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

/* Тени для текста */
span.text-shadow {
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
}

/* Применение курсора при наведении */
span.clickable {
    cursor: pointer;
}

/* Применение анимации при наведении */
span.hover-animate:hover {
    color: #ff6347; /* Меняем цвет текста при наведении */
    transition: color 0.3s ease-in-out;
}

.cell {
    min-width: 50px;
    min-height: 50px;
    width: auto;
    height: auto;
    border-top: 1px solid #4B0082;
    border-left: 1px solid #4B0082;
    cursor: pointer;
    transition: 0.2s;
}

.cell:hover {
    background-color: whitesmoke;
}

.legend {
    background-color: khaki;
    font-style: italic;
}

.legend:hover {
    background-color: #f0d28c;
}

.row {
    display: flex;
}

.row:last-child .cell {
    border-bottom: 1px solid #4B0082; /* Только нижняя граница для последней строки */
}

.cell:last-child {
    border-right: 1px solid #4B0082; /* Только правая граница для последнего столбца */
}

.cell.no-border-left {
    border-left: none; /* убираем границу */
    padding-left: 1px; /* добавляем отступ, чтобы сохранить размер */
}

.cell.no-border-top {
    border-top: none; /* убираем границу */
    padding-top: 1px; /* добавляем отступ, чтобы сохранить размер */
}

table {
    width: 100%;
    border-collapse: collapse;
    text-align: center;
}

th, td {
    border: 1px solid #ddd;
    padding: 8px;
}

th {
    background-color: #f2f2f2;
    font-weight: bold;
}

.photo-cell {
    width: 200px;
    height: 200px;
}

.photo-cell img {
    width: 100%;
    height: 100%;
    cursor: pointer;
    border-radius: 20px;
    object-fit: cover;
}